<template>
  <div>
    <el-row>
      <el-col :span="24">
        <h2 style="font: 20px Extra large;">{{articleVO.title}}</h2>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="8">
        <span class="span">{{ articleVO.staffName }}</span>
      </el-col>
      <el-col :span="8">
        <span class="span">{{ articleVO.depName }}</span>
      </el-col>
    </el-row>
    <div style="margin: 20px 0;"></div>
    <el-row>
      <el-col :span="24" class="content">{{articleVO.articleContent}}</el-col>
    </el-row>
    <div style="margin: 20px 0;"></div>
    <comments></comments>
  </div>
</template>

<script>
import Qs from "qs";
import Comment from "./Comment";

export default {
  components: {
    comments: Comment
  },
  data() {
    return {
      articleVO: {}
    };
  },
  methods: {
    getData() {
      var api =
        "http://localhost:8888/article/detail/" + this.$route.params.articleID;
      this.axios
        .get(api)
        .then(response => {
          console.log(response);
          console.log(response.data);
          this.articleVO = response.data;
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  created() {
    this.getData();
  }
};
</script>

<style lang="scss" scoped>
.content {
  border-radius: 2px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
}
.span {
  color: #303133;
  background: #909399;
  display: inline-block;
  border-radius: 30px;
  font: 16px Medium;
}
</style>