<template>
  <div>
    <el-row>
      <el-col :span="18">
        <el-input
          type="textarea"
          :autosize="{ minRows: 4, maxRows: 6}"
          placeholder="请输入评论内容..."
          v-model="comment.comment"
        ></el-input>
      </el-col>
    </el-row>
    <div style="margin: 20px 0;"></div>
    <el-row type="flex" justify="center">
      <el-col :span="12">
        <el-button type="success" @click="Post()" round>发表评论</el-button>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center">
      <el-col :span="24">
        <h3 style="color: #909399;margin-left: 10px">员工评论</h3>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24" align="left">
        <ul style="padding-left: 10px;">
          <li class="list-group" v-for="item in comments" :key="item.commentID">
            {{ item.comment }}
            <div class="span">
              <span class="reply">{{ item.staffName }}</span>
              <span class="reply">{{ item.depName }}</span>
            </div>
          </li>
        </ul>
      </el-col>
    </el-row>
    <el-row>
      <el-col>
        <el-pagination
          background
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="10"
          layout="total, prev, pager, next"
          :total="total"
        ></el-pagination>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Qs from "qs";

export default {
  inject: ["reload"],
  data() {
    return {
      currentPage: 1,
      total: 0,
      comment: {
        articleID: sessionStorage.getItem("articleID"),
        staffID: sessionStorage.getItem("staffID"),
        depID: sessionStorage.getItem("depID"),
        comment: ""
      },
      comments: []
    };
  },
  methods: {
    handleCurrentChange(current) {
      this.currentPage = current;
      var api = "http://localhost:8888/article/comment/" + sessionStorage.getItem("articleID") + "/" + current + "/" + 10;
      this.axios
        .get(api)
        .then(response => {
          console.log("分页响应对象：" + response);
          this.articles = response.data.records;
          this.total = response.data.total;
        })
        .catch(error => {
          console.log(error);
        });
    },
    //   发表评论
    Post() {
      console.log("post对象：" + JSON.stringify(this.comment));
      var api = "http://localhost:8888/article/comment/add";
      this.axios
        .post(api, Qs.stringify(this.comment))
        .then(response => {
          console.log(response);
          if (response.status == 200) {
            this.$message({
              showClose: true,
              message: "操作成功",
              type: "success"
            });
            this.reload();
          }
        })
        .catch(error => {
          console.log(error);
          this.$message({
            showClose: true,
            message: "操作失败，请联系维护人员",
            type: "error"
          });
        });
    },
    getData() {
      var api =
        "http://localhost:8888/article/comment/" +
        sessionStorage.getItem("articleID") +
        "/" +
        1 +
        "/" +
        10;
      this.axios
        .get(api)
        .then(response => {
          console.log(response);
          console.log(response.data);
          this.comments = response.data.records;
          this.total = response.data.total;
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  created() {
    this.getData();
  }
};
</script>

<style lang="scss" scoped>
.list-group {
  list-style-type: none;
  width: 800px;
  height: 50px;
  border-radius: 4px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  margin-bottom: 5px;
}
.span {
  width: 800px;
  text-align: right;
}
.reply {
  color: #303133;
  background: #409EFF;
  display: inline-block;
  border-radius: 30px;
  font: 12px Extra Small;
}
</style>